.backdrop {
    z-index: 2;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    opacity: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    /* align-content: center; */
    background-color: rgba(0, 0, 0, 0.5);
    transition: 0.6s;
    pointer-events: none;
}

.backdropActive {
    opacity: 1;
    pointer-events: all;
    overflow-y: scroll;
    overscroll-behavior: contain;
}

.modalContent {
    margin-top: 40px;
    padding: 20px;
    border-radius: 10px;
    background-color: #fff;
    z-index: 1000;
    position: absolute;
    transform: translateY(-100%);
    min-width: 100px;
    max-width: 902px;
    transition: 0.5s all;
    ;
    box-shadow: 0px 2px 1px 0px rgba(0, 0, 0, 0.20), 0px 1px 3px 0px rgba(0, 0, 0, 0.12), 0px 1px 1px 0px rgba(0, 0, 0, 0.14);
}

.modalContentActive {
    transform: translateY(25%);
}

.button {
    border: inherit;
    background-color: inherit;
    position: absolute;
    top: 20px;
    right: 20px;
}

@media screen and (min-width: 768px) {
    .modalContentActive {
        transform: translateY(15%);
    }
}

@media screen and (min-width: 1440px) {
    .modalContentActive {
        transform: translateY(5%);
    }
}